<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <!--  -->
          <el-input class="input-with-select" clearable placeholder="" v-model="queryInfo.query"
                    v-on:keydown.enter.native="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
<!--        <el-col :span="4">-->
<!--          <el-button type="primary" @click="addDialogVisible = true"></el-button>-->
<!--        </el-col>-->
      </el-row>
    </el-card>

    <!--  -->
    <el-table v-loading="loading"
              element-loading-text=""
              :data="userList"
              :stripe="true"
              border
              style="width: 100%">
<!--      <el-table-column label="#"-->
<!--                       type="index">-->
<!--      </el-table-column>-->
      <el-table-column label="id"
                       width="70"
                       prop="uid">
      </el-table-column>
      <el-table-column label=""
                       prop="username" width="100">
      </el-table-column>
      <el-table-column label=""
                       prop="phone" width="120">
      </el-table-column>
      <el-table-column label=""
                       prop="email">
      </el-table-column>
      <el-table-column label="QQ"
                       prop="qq" width="110">
      </el-table-column>
      <el-table-column label=""
                       prop="gender" width="50">
        <template slot-scope="scope">
          <span v-if="scope.row.gender === 1" style="color:blue"></span>
          <span v-else style="color: red"></span>
        </template>
      </el-table-column>
      <el-table-column label=""
                       prop="createdTime" width="160">
      </el-table-column>
      <el-table-column label=""
                       prop="modifiedTime" width="160">
      </el-table-column>
      <el-table-column label=""
                       prop="modifiedUser" width="100">
      </el-table-column>
      <el-table-column label="" width="80">
        <!--  -->
        <template slot-scope="scope">
          <el-switch
              v-model="scope.row.isDelete"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="0"
              :inactive-value="1"
              @change="userStateChanged(scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="130">
        <template slot-scope="scope">
          <!--  -->
          <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
            <el-button icon="el-icon-edit" size="mini" type="primary"
                       @click="showEditDialog(scope.row.uid)"></el-button>
          </el-tooltip>

          <!-- -->
          <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
            <el-button icon="el-icon-delete" size="mini" type="danger"
                       @click="removeUserById(scope.row.uid)"></el-button>
          </el-tooltip>

          <!-- -->
<!--          <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">-->
<!--            <el-button icon="el-icon-setting" size="mini" type="warning" @click="setRole(scope.row)"></el-button>-->
<!--          </el-tooltip>-->
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-pagination
        :current-page="queryInfo.pageNum"
        :page-size="this.queryInfo.pageSize"
        :page-sizes="[6, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
    </el-pagination>

    <!---->
    <el-dialog
        :visible.sync="editDialogVisible"
        title=""
        width="35%"
        @close="editDialogClosed">
      <!--  -->
      <el-form ref="editFormRef" :model="editForm" :rules="addFormRules" label-width="70px">
        <!-- prop=username  addFormRulesusername-->
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="qq">
          <el-input v-model="editForm.qq"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editForm.phone" w></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.avatar"></el-input>
          <el-image style="width: 120px" :src="editForm.avatar"></el-image>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.message"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="editForm.gender" :label="1"></el-radio>
          <el-radio v-model="editForm.gender" :label="0"></el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUserInfo"> </el-button>
  </span>
    </el-dialog>



  </div>
</template>

<script>
import {reqGetUserListByPage,reqUpdateIsDelete,reqSelectUserByUid,reqUpdateUserInfo,reqDeleteUserByUid} from '@/api'
export default {
  name: "UserList",
  data(){
    // 
    let checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (!value){
        // 
        return cb()
      }
      if (regEmail.test(value)) {
        // 
        return cb()
      }
      cb(new Error('!'))
    }
    // 
    let checkMobile = (rule, value, cb) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        // 
        return cb()
      }
      cb(new Error('!'))
    }
    return {
      // 
      queryInfo: {
        query: '',
        // 
        pageNum: 1,
        pageSize: 6
      },
      total: 0,
      //
      editDialogVisible:false,
      userList:[],
      loading:false,
      // 
      editForm: {
        uid: '',
        username: '',
        qq: '',
        email: '',
        phone: '',
        message: '',
        gender:'',
        avatar:'',
      },
      // 
      addFormRules: {
        email: [{
          required: false,
          message: '',
          trigger: 'blur'
        }, {
          validator: checkEmail,
          trigger: 'blur'
        }],
        mobile: [
          {
            required: false,
            message: '',
            trigger: 'blur'
          }, {
            validator: checkMobile,
            trigger: 'blur'
          }
        ]
      },
    }
  },
  created() {
    this.getUserList()
  },
  methods:{
    //
    async getUserList(){
      this.loading = true
      try {
        let result = await reqGetUserListByPage({
          'currentPage':this.queryInfo.pageNum,'size':this.queryInfo.pageSize,'keyword':this.queryInfo.query});
        this.userList = result.data.records
        this.total = result.data.total
      }catch (e) {
        console.log(e.message)
      }finally {
        this.loading = false
      }
    },
    /**
     *  pagesize 
     * @param newSize
     */
    async handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.pageSize = newSize
      await this.getUserList()
    },
    /**
     *   
     * @param newPage
     */
    handleCurrentChange (newPage) {
      // console.log(newPage)
      this.queryInfo.pageNum = newPage
      this.getUserList()
    },
    //
    async userStateChanged (userInfo) {
      let message
      if (userInfo.isDelete === 1){
        message = ', ?'
      }else {
        message = ', ?'
      }
      this.$confirm(message, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await reqUpdateIsDelete({'uid':userInfo.uid, 'isDelete':userInfo.isDelete})
          await this.getUserList()
        }catch (e) {
          console.log(e.message)
        }
      }).catch(() => {
        userInfo.isDelete = userInfo.isDelete === 0 ? 1 : 0
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    /**
     * 
     * @param id
     */
    async showEditDialog (id) {
      try {
        let result = await reqSelectUserByUid({'uid':id})
        this.editForm = result.data
        this.editDialogVisible = true
      }catch (e) {
        console.log(e.message())
      }
    },
    /**
     * 
     */
    async editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        const {uid, username, qq, email, phone, message, gender, avatar} = this.editForm
        // 
        try {
          await reqUpdateUserInfo({uid, username, qq, email, phone, message, gender, avatar})
          // 
          this.editDialogVisible = false
          // , 
          await this.getUserList()
        }catch (e) {
          console.log(e.message)
        }
      })
    },
    /**
     * Id
     * @param id
     */
    async removeUserById (id) {
      // console.log(id)
      // 
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await reqDeleteUserByUid({'uid':id})
          await this.getUserList()
        }catch (e) {
          console.log(e.message)
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
    /**
     * 
     */
    editDialogClosed () {
      // , , 
      // 
      this.$refs.editFormRef.resetFields()
    },
  },
}
</script>

<style scoped>

</style>
