<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <el-row :gutter="8">
        <!--  -->
        <el-col :span="4">
          <el-input v-model="queryInfo.keyword" size="small" class="input-with-select" clearable placeholder=""
                    @clear="getGoodsList" @keyup.enter.native="getGoodsList">
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-input v-model="queryInfo.ownerId" size="small" class="input-with-select" clearable placeholder="ID"
                    @clear="getGoodsList" @keyup.enter.native="getGoodsList">
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-dropdown @command="handleCommand">
            <el-button type="primary" size="small">
              {{goodsStatus}}<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item :command="-4"><el-tag></el-tag></el-dropdown-item>
              <el-dropdown-item :command="-3"><el-tag type="danger"></el-tag></el-dropdown-item>
              <el-dropdown-item :command="-2"><el-tag type="info"></el-tag></el-dropdown-item>
              <el-dropdown-item :command="-1"><el-tag type="warning"></el-tag></el-dropdown-item>
              <el-dropdown-item :command="0"><el-tag type="success"></el-tag></el-dropdown-item>
              <el-dropdown-item command=""></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
        <el-col :span="1">
          <el-button size="small" icon="el-icon-search" @click="getGoodsList"></el-button>
        </el-col>
<!--        <el-col :span="4">-->
<!--          <el-button type="primary" @click="goAddpage"></el-button>-->
<!--        </el-col>-->
      </el-row>

      <!--  -->
      <el-table v-loading="loading"
                :data="goodsList"
                :stripe="true"
                border
                element-loading-text=""
                width="100%">
        <el-table-column label="id"
                         width="70"
                         prop="id">
        </el-table-column>
        <el-table-column label=""
                         prop="name">
        </el-table-column>
        <el-table-column label="id"
                         prop="ownerId" width="80px">
        </el-table-column>
        <el-table-column label="()"
                         prop="price" width="105px">
        </el-table-column>
        <el-table-column label=""
                         prop="num" width="60px">
        </el-table-column>
        <el-table-column label=""
                         prop="createDate" width="160px">
        </el-table-column>
        <el-table-column label="" prop="goods_state" width="95px">
          <template slot-scope="scope">
            <el-dropdown placement="top-end">
              <el-link :underline="false">
                <el-tag v-if="scope.row.status === -4"></el-tag>
                <el-tag v-else-if="scope.row.status === -3" type="danger"></el-tag>
                <el-tag v-else-if="scope.row.status === -2" type="info"></el-tag>
                <el-tag v-else-if="scope.row.status === -1" type="warning"></el-tag>
                <el-tag v-else type="success">:{{scope.row.status}}</el-tag>
              </el-link>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-if="scope.row.status!==-4" @click.native="updateStatus('',-4,scope.row.id,scope.row.name)"><el-tag></el-tag></el-dropdown-item>
                <el-dropdown-item v-if="scope.row.status!==-3" @click.native="updateStatus('',-3,scope.row.id,scope.row.name)"><el-tag type="danger"></el-tag></el-dropdown-item>
                <el-dropdown-item v-if="scope.row.status!==-2" @click.native="updateStatus('',-2,scope.row.id,scope.row.name)"><el-tag type="info"></el-tag></el-dropdown-item>
                <el-dropdown-item v-if="scope.row.status!==-1" @click.native="updateStatus('',-1,scope.row.id,scope.row.name)"><el-tag type="warning"></el-tag></el-dropdown-item>
                <el-dropdown-item v-if="scope.row.status<0" @click.native="updateStatus('',0,scope.row.id,scope.row.name)"><el-tag type="success"></el-tag></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
              <el-button icon="el-icon-search" size="mini" type="primary"
                         @click="viewGoodsById(scope.row.id)"></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
              <el-button icon="el-icon-delete" size="mini" type="danger"
                         @click="removeGoodsById(scope.row.id,scope.row.name)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
          :current-page="queryInfo.currentPage"
          :page-size="queryInfo.size"
          :page-sizes="[5,10,15,20]"
          :total="total"
          background
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>



    <el-dialog  :visible.sync="dialogVisible" append-to-body width="1250px"
                style="padding: 0;" title="">
      <GoodsDetailInfo v-bind:goodsDetailInfo="goodsDetailInfo"/>
    </el-dialog>
  </div>
</template>

<script>
import {reqSearchGoodsInfoList, reqDeleteGoodsById, reqUpdateGoodsStatusByGoodsId,
  reqSelectGoodsByGoodsId} from '@/api'
import GoodsDetailInfo from '@/views/GoodsDetailInfo'
export default {
  name: "GoodsList",
  components: {
    GoodsDetailInfo
  },
  data () {
    return {
      // 
      queryInfo: {
        keyword: '',
        currentPage: 1,
        size: 5,
        categoryId:'',
        typeId:'',
        status:'',
        ownerId:'',
      },
      // 
      total: 0,
      // 
      goodsList: [],
      // 
      goodsStatus:'',
      loading:false,
      // 
      updateGoodsStatus:'',
      //
      goodsDetailInfo:{},
      //
      dialogVisible:false,
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    /**
     * 
     */
    async getGoodsList () {
      /**
       * get,  params
       */
      this.loading = true
      try {
        let result = await reqSearchGoodsInfoList(this.queryInfo)
        this.goodsList = result.data.records
        this.total = result.data.total
      }catch (e) {
        console.log(e.message)
      }finally {
        this.loading = false
      }
    },
    handleSizeChange (newSize) {
      this.queryInfo.size = newSize
      this.getGoodsList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.currentPage = newPage
      this.getGoodsList()
    },
    //
    handleCommand(command){
      this.queryInfo.status = command
      if (command === -4){
        this.goodsStatus = ''
      }else if (command === -3){
        this.goodsStatus = ''
      }else if (command === -2){
        this.goodsStatus = ''
      }else if (command === -1){
        this.goodsStatus = ''
      }else if (command === 0){
        this.goodsStatus = ''
      }else {
        this.goodsStatus = ''
      }
    },
    //
    // updateCommand(goodsStatus){
    //   this.updateGoodsStatus = goodsStatus
    // },
    //
    updateStatus(goodsStatusName,goodsStatus,goodsId,goodsName){
      this.$confirm('<' + goodsName + '><' + goodsStatusName + '>, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await reqUpdateGoodsStatusByGoodsId({goodsId,goodsStatus})
        await this.getGoodsList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    /**
     * @param goodsId id
     * @param goodsName 
     */
    async removeGoodsById (goodsId,goodsName) {
      await this.$confirm('<'+goodsName+'>, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await reqDeleteGoodsById({goodsId})
          if (this.total % this.queryInfo.size === 1){
            this.queryInfo.currentPage--
            await this.getGoodsList()
          }else {
            await this.getGoodsList()
          }
        }catch (e) {
          console.log(e.message)
        }
      }).catch(err => {
        return err
      })
    },
    /**
     * @param goodsId id
     */
    async viewGoodsById(goodsId){
      try {
        let result = await reqSelectGoodsByGoodsId({goodsId})
        this.goodsDetailInfo = result.data
        this.dialogVisible = true
      }catch (e) {
        console.log(e.message)
      }
    },
  },
}
</script>

<style scoped>

</style>
