import moment from 'moment';
/**
 * @param {String} url
 * @description URL
 */
export const getParams = url => {
  const keyValueArr = url.split('?')[1].split('&');
  let paramObj = {};
  keyValueArr.forEach(item => {
    const keyValue = item.split('=');
    paramObj[keyValue[0]] = keyValue[1];
  });
  return paramObj;
};

/**
 * @param {Any} obj
 * @description 
 */
export const getType = obj => {
  return {}.toString
    .call(obj)
    .match(/\s([a-zA-Z]+)/)[1]
    .toLowerCase();
};
// 
export const dateFormat = {
  YMD: 'YMD',
  YMDHM: 'YMDHM',
  YMDHMS: 'YMDHMS'
};
export const forEach = (arr, fn) => {
  if (!arr.length || !fn) return;
  let i = -1;
  let len = arr.length;
  while (++i < len) {
    let item = arr[i];
    fn(item, i, arr);
  }
};

/**
 * @param {Array} arr1
 * @param {Array} arr2
 * @description , 
 */
export const getIntersection = (arr1, arr2) => {
  let len = Math.min(arr1.length, arr2.length);
  let i = -1;
  let res = [];
  while (++i < len) {
    const item = arr2[i];
    if (arr1.indexOf(item) > -1) res.push(item);
  }
  return res;
};

/**
 * @param {Array} arr1
 * @param {Array} arr2
 * @description , 
 */
export const getUnion = (arr1, arr2) => {
  return Array.from(new Set([...arr1, ...arr2]));
};

/**
 * @param {Array} target 
 * @param {Array} arr 
 * @description 
 */
export const hasOneOf = (targetarr, arr) => {
  return targetarr.some(_ => arr.indexOf(_) > -1);
};

/**
 * @param {String|Number} value 
 * @param {*} validList 
 */
export function oneOf (value, validList) {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true;
    }
  }
  return false;
}

/**
 * @param {Number} timeStamp 
 * @returns {Boolean}
 */
const isMillisecond = timeStamp => {
  const timeStr = String(timeStamp);
  return timeStr.length > 10;
};

/**
 * @param {Number} timeStamp 
 * @param {Number} currentTime 
 * @returns {Boolean} 
 */
const isEarly = (timeStamp, currentTime) => {
  return timeStamp < currentTime;
};

/**
 * @param {Number} num 
 * @returns {String} 
 * @description 1010
 */
const getHandledValue = num => {
  return num < 10 ? '0' + num : num;
};

/**
 * @param {Number} timeStamp 
 * @param {Number} startType 'year'
 */
const getDate = (timeStamp, startType) => {
  const d = new Date(timeStamp * 1000);
  const year = d.getFullYear();
  const month = getHandledValue(d.getMonth() + 1);
  const date = getHandledValue(d.getDate());
  const hours = getHandledValue(d.getHours());
  const minutes = getHandledValue(d.getMinutes());
  const second = getHandledValue(d.getSeconds());
  let resStr = '';
  if (startType === 'year') {
    resStr =
      year +
      '-' +
      month +
      '-' +
      date +
      ' ' +
      hours +
      ':' +
      minutes +
      ':' +
      second;
  }
  else resStr = month + '-' + date + ' ' + hours + ':' + minutes;
  return resStr;
};

/**
 * @param {String|Number} timeStamp 
 * @returns {String} 
 */
export const getRelativeTime = timeStamp => {
  // 
  const IS_MILLISECOND = isMillisecond(timeStamp);
  // 
  if (IS_MILLISECOND) Math.floor((timeStamp /= 1000));
  // 
  timeStamp = Number(timeStamp);
  // 
  const currentTime = Math.floor(Date.parse(new Date()) / 1000);
  // 
  const IS_EARLY = isEarly(timeStamp, currentTime);
  // 
  let diff = currentTime - timeStamp;
  // IS_EARLYfalse
  if (!IS_EARLY) diff = -diff;
  let resStr = '';
  const dirStr = IS_EARLY ? '' : '';
  // 59
  if (diff <= 59) resStr = diff + '' + dirStr;
  // 595959
  else if (diff > 59 && diff <= 3599) { resStr = Math.floor(diff / 60) + '' + dirStr; }
  // 5959235959
  else if (diff > 3599 && diff <= 86399) { resStr = Math.floor(diff / 3600) + '' + dirStr; }
  // 235959295959
  else if (diff > 86399 && diff <= 2623859) { resStr = Math.floor(diff / 86400) + '' + dirStr; }
  // 295959364235959
  else if (diff > 2623859 && diff <= 31567859 && IS_EARLY) { resStr = getDate(timeStamp); }
  else resStr = getDate(timeStamp, 'year');
  return resStr;
};

/**
 * @returns {String} 
 */
export const getExplorer = () => {
  const ua = window.navigator.userAgent;
  const isExplorer = exp => {
    return ua.indexOf(exp) > -1;
  };
  if (isExplorer('MSIE')) return 'IE';
  else if (isExplorer('Firefox')) return 'Firefox';
  else if (isExplorer('Chrome')) return 'Chrome';
  else if (isExplorer('Opera')) return 'Opera';
  else if (isExplorer('Safari')) return 'Safari';
};

/**
 * @description  on(element, event, handler)
 */
export const on = (function () {
  if (document.addEventListener) {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false);
      }
    };
  } else {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.attachEvent('on' + event, handler);
      }
    };
  }
})();

/**
 * @description  off(element, event, handler)
 */
export const off = (function () {
  if (document.removeEventListener) {
    return function (element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false);
      }
    };
  } else {
    return function (element, event, handler) {
      if (element && event) {
        element.detachEvent('on' + event, handler);
      }
    };
  }
})();

/**
 * keykeyobjkey
 * keyobj
 */
export const hasKey = (obj, key) => {
  if (key) return key in obj;
  else {
    let keysArr = Object.keys(obj);
    return keysArr.length;
  }
};

/**
 * @param {*} obj1 
 * @param {*} obj2 
 * @description 
 */
export const objEqual = (obj1, obj2) => {
  const keysArr1 = Object.keys(obj1);
  const keysArr2 = Object.keys(obj2);
  if (keysArr1.length !== keysArr2.length) return false;
  else if (keysArr1.length === 0 && keysArr2.length === 0) return true;
  /* eslint-disable-next-line */ else { return !keysArr1.some(key => obj1[key] != obj2[key]); }
};

// 
export const utils = {
  /**
   * @description table
   * @param {Object} vm Vue
   * @param {Array} tableSelectDate 
   * @param {Array} allData 
   * @param {Array} key 
   */
  reverseSelect (vm, tableSelectDate, allData, key) {
    let copyMess = JSON.parse(JSON.stringify(tableSelectDate));
    // ->
    vm.handleSelectAll(false);
    // idList
    let idList = copyMess.map(item => item[key]);
    console.log(idList);
    for (let item of allData) {
      if (idList.every(id => id !== item.id)) {
        vm.$set(item, '_checked', true);
        tableSelectDate.push(item);
      } else {
        vm.$set(item, '_checked', false);
      }
    }
  },
  //  
  contrastString (originStr, changeStr) {
    let origin = originStr
      .replace(/\s*/g, '')
      .replace(/"/g, '\'')
      .replace(/&nbsp;/g, '')
      .replace(/disabled=\/'\/'/g, 'disabled');
    let change = changeStr
      .replace(/\s*/g, '')
      .replace(/"/g, '\'')
      .replace(/&nbsp;/g, '')
      .replace(/disabled=\/'\/'/g, 'disabled');
    return origin === change;
  },
  // getDateStr(0)getDateStr(-10)getDateStr(20)
  getDateStr (AddDayCount, format) {
    let date = new Date();
    // AddDayCount
    date.setDate(date.getDate() + AddDayCount);
    return this.getDate(date, format);
  },
  getDate (date, format) {
    let year = date.getFullYear();
    // day100
    let day = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
    // month100
    let month =
      date.getMonth() + 1 > 9
        ? date.getMonth() + 1
        : '0' + (date.getMonth() + 1);
    // h100
    let h = date.getHours() > 9 ? date.getHours() : '0' + date.getHours();
    // s100
    let m = date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes();
    // s100
    let s = date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds();
    let resultDate = '';
    if (format === dateFormat.YMD) {
      resultDate = year + '-' + month + '-' + day;
    }
    if (format === dateFormat.YMDHM) {
      resultDate = year + '-' + month + '-' + day + ' ' + h + ':' + m;
    }
    if (format === dateFormat.YMDHMS) {
      resultDate = year + '-' + month + '-' + day + ' ' + h + ':' + m + ':' + s;
    }
    return resultDate;
  },
  // 
  getDateWeek () {
    let now = new Date();
    let nowTime = now.getTime();
    let day = now.getDay();
    let oneDayLong = 1000 * 60 * 60 * 24;
    let MondayTime = nowTime - (day - 1) * oneDayLong;
    let SundayTime = nowTime + (7 - day) * oneDayLong;
    let monday = new Date(MondayTime);
    let sunday = new Date(SundayTime);
    return {
      // first: this.getDateAll(monday),
      // last: this.getDateAll(sunday),
      firstDate: monday,
      lastDate: sunday
    };
  },
  // 
  getDateMonth () {
    let dateFirter = new Date();
    let dateLast = new Date();
    dateFirter.setDate(1);

    let currentMonth = dateLast.getMonth();
    let nextMonth = ++currentMonth;
    let nextMonthFirstDay = new Date(dateLast.getFullYear(), nextMonth, 1);
    let oneDay = 1000 * 60 * 60 * 24;
    dateLast = new Date(nextMonthFirstDay - oneDay);

    return {
      // first: this.getDateAll(dateFirter),
      // last: this.getDateAll(dateLast),
      firstDate: dateFirter,
      lastDate: dateLast
    };
  },
  // 
  getDayBetweenDate (date) {
    date = this.getDate(new Date(date), 'YMD');
    let startTime = Date.parse(new Date(date)); // IEyyyy/MM/dd
    let endTime = Date.parse(this.getDate(new Date(), 'YMD'));
    let day = parseInt((endTime - startTime) / (1000 * 60 * 60 * 24));
    return day;
  },
  getDateIntervalYear (firstDate, secondDate) {
    if (!firstDate || !secondDate) {
      return 0;
    }
    let first = new Date(firstDate);
    let second = new Date(secondDate);
    let firstYear = first.getFullYear();
    let secondYear = second.getFullYear();
    let intervalYear = secondYear - firstYear;
    return intervalYear < 0 ? 0 : intervalYear;
  },
  getDateIntervalYearFixed2 (firstDate, secondDate) {
    if (!firstDate || !secondDate) {
      return 0;
    }
    // 
    let startDate = new Date(this.getDate(new Date(firstDate), 'YMD'));
    let endDate = new Date(this.getDate(new Date(secondDate), 'YMD'));
    // 
    let startTime = Date.parse(startDate);
    let endTime = Date.parse(endDate);
    // 
    let day = parseInt((endTime - startTime) / (1000 * 60 * 60 * 24));
    if (day <= 0) {
      return 0;
    }
    //  
    let resultYear = parseFloat((day / (30 * 12)).toFixed(2));
    return resultYear;
  },
  // 
  // 
  convertCurrency (money) {
    // 
    let cnNums = ['', '', '', '', '', '', '', '', '', ''];
    // 
    let cnIntRadice = ['', '', '', ''];
    // 
    let cnIntUnits = ['', '', '', ''];
    // 
    let cnDecUnits = ['', '', '', ''];
    // 
    let cnInteger = '';
    // 
    let cnIntLast = '';
    // 
    let maxNum = 999999999999999.9999;
    // 
    let integerNum;
    // 
    let decimalNum;
    // 
    let chineseStr = '';
    // 
    let parts;
    if (money === '') {
      return '';
    }
    money = parseFloat(money);
    if (money >= maxNum) {
      // 
      return '';
    }
    if (money === 0) {
      chineseStr = cnNums[0] + cnIntLast + cnInteger;
      return chineseStr;
    }
    // 
    money = money.toString();
    if (money.indexOf('.') === -1) {
      integerNum = money;
      decimalNum = '';
    } else {
      parts = money.split('.');
      integerNum = parts[0];
      decimalNum = parts[1].substr(0, 4);
    }
    // 
    if (parseInt(integerNum, 10) > 0) {
      let zeroCount = 0;
      let IntLen = integerNum.length;
      for (let i = 0; i < IntLen; i++) {
        let n = integerNum.substr(i, 1);
        let p = IntLen - i - 1;
        let q = p / 4;
        let m = p % 4;
        if (n === '0') {
          zeroCount++;
        } else {
          if (zeroCount > 0) {
            chineseStr += cnNums[0];
          }
          // 
          zeroCount = 0;
          chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
        }
        if (m === 0 && zeroCount < 4) {
          chineseStr += cnIntUnits[q];
        }
      }
      chineseStr += cnIntLast;
    }
    // 
    if (decimalNum !== '') {
      let decLen = decimalNum.length;
      for (let i = 0; i < decLen; i++) {
        let n = decimalNum.substr(i, 1);
        if (n !== '0') {
          chineseStr += cnNums[Number(n)] + cnDecUnits[i];
        }
      }
    }
    if (chineseStr === '') {
      chineseStr += cnNums[0] + cnIntLast + cnInteger;
    } else if (decimalNum === '') {
      chineseStr += cnInteger;
    }
    return chineseStr;
  }
};


export const dateTimeRangeConvert = (timerange) => {
  // timerange
  let arr = [];
  if (timerange[0] === '') {
    arr.push(null);
  } else {
    arr.push(moment(timerange[0]).format("YYYY-MM-DD 00:00:00"));
  }

  if (timerange[1] === '') {
    arr.push(null);
  } else {
    arr.push(moment(timerange[1]).format("YYYY-MM-DD 23:59:59"));
  }
  return arr;
}

