<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddWarnDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
          :columns="warnColumns"
          :data="warnList"
          :expand-type="false"
          :selection-type="false"
          :show-index="true"
          :show-row-hover="true"
          border
          class="tree-table"
          index-text="#">
        <!---->
<!--        <template slot="order" slot-scope="scope">-->
<!--          <el-tag v-if="!scope.row.fatherId" size="mini"></el-tag>-->
<!--          <el-tag v-else size="mini" type="success"></el-tag>-->
<!--        </template>-->
        <!---->
        <template slot="opt" slot-scope="scope">
          <el-button icon="el-icon-edit" size="mini" type="primary" @click="showEditDialog(scope.row.warnId)">
          </el-button>
          <el-button icon="el-icon-delete" size="mini" type="danger" @click="removeWarnById(scope.row.warnId)">
          </el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-dialog :visible.sync="addWarnDialogVisible" title="" @close="addWarnDialogClosed">
        <el-form ref="addWarnFormRef" :model="addWarnForm" :rules="addWarnFormRules" label-width="100px">
          <el-form-item label=": " prop="warnName">
            <el-input v-model="addWarnForm.warnName"></el-input>
          </el-form-item>
          <el-form-item label=": " prop="warnDetail">
            <el-input v-model="addWarnForm.warnDetail"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addWarnDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addWarn"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog :visible.sync="editWarnDialogVisible" title="" @close="editWarnDialogClosed">
        <el-form ref="editWarnFormRef" :model="editWarnForm" label-width="100px">
          <el-form-item label=": " prop="warnName">
            <el-input v-model="editWarnForm.warnName"></el-input>
          </el-form-item>
          <el-form-item label=": " prop="warnDetail">
            <el-input v-model="editWarnForm.warnDetail"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="editWarnDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editWarn"> </el-button>
        </div>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
import {
  reqAddGoodsWarn,
  reqGetGoodsWarnList,
  reqGetGoodsWarnByWarnId,
  reqDeleteGoodsWarnByWarnId,
  reqUpdateGoodsWarnByWarnId
} from '@/api'
export default {
  name: "GoodsWarn",
  data(){
    return{
      // , 
      warnList: [],
      // 
      addWarnDialogVisible:false,
      // 
      editWarnDialogVisible:false,
      // 
      addWarnForm: {
        warnId: '',
        warnName: '',
        warnDetail: '',
        warnCreate: '',
        warnUpdate: '',
      },
      addWarnFormRules: {
        warnName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        warnDetail: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
      },
      // 
      editWarnForm: {},
      // table
      warnColumns: [
        {
          label: 'ID',
          prop: 'warnId'
        },
        {
          label: '',
          prop: 'warnName'
        },
        {
          label: '',
          prop: 'warnDetail'
        },
        {
          label: '',
          prop: 'warnCreate'
        },
        {
          label: '',
          prop: 'warnUpdate'
        },
        {
          label: '',
          // , 
          type: 'template',
          // 
          template: 'opt'
        },
      ],
    }
  },
  created () {
    this.getWarnList()
  },
  methods:{
    /**
     * 
     */
    async getWarnList () {
      let result = await reqGetGoodsWarnList()
      this.warnList = result.data
    },
    /**
     * 
     * @param warnId
     */
    async showEditDialog (warnId) {
      try {
        let result = await reqGetGoodsWarnByWarnId({warnId})
        this.editWarnForm = result.data
        this.editWarnDialogVisible = true
      }catch (e) {
        console.log(e.message)
      }
    },
    /**
     * id
     * @param warnId id
     */
    async removeWarnById (warnId) {
      // 
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await reqDeleteGoodsWarnByWarnId({warnId})
          await this.getWarnList()
        }catch (e) {
          console.log(e.message)
        }
      }).catch(async () => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    /**
     * , 
     */
    showAddWarnDialog () {
      // 
      // this.getParentCateList()
      // 
      this.addWarnDialogVisible = true
    },
    /**
     * , 
     */
    async addWarn () {
      this.$refs.addWarnFormRef.validate(async valid => {
        if (!valid) return
        const {warnName, warnDetail} = this.addWarnForm
        try {
          await reqAddGoodsWarn({warnName,warnDetail})
          this.addWarnDialogVisible = false
          await this.getWarnList()
        }catch (e) {
          console.log(e.message)
        }
      })
    },
    /**
     * , 
     */
    addWarnDialogClosed () {
      this.$refs.addWarnFormRef.resetFields()
      this.addWarnForm = {}
    },
    /**
     * 
     */
    async editWarn () {
      this.$refs.editWarnFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        try {
          const {warnName,warnDetail,warnId} = this.editWarnForm
          await reqUpdateGoodsWarnByWarnId({warnId,warnName,warnDetail})
          await this.getWarnList()
          this.editWarnDialogVisible = false
        }catch (e) {
          console.log(e.message)
        }
      })
    },
    /**
     *  
     */
    editWarnDialogClosed () {
      this.$refs.editWarnFormRef.resetFields()
      this.editWarnForm = {}
    },
  },
}
</script>

<style scoped>

</style>
