<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-table v-loading="loading"
              :data="purchaseInfoList"
              :stripe="true"
              border
              element-loading-text=""
              width="100%">
      <el-table-column label="id"
                       width="60"
                       prop="id">
      </el-table-column>
      <el-table-column label=""
                       prop="title" width="200px">
      </el-table-column>
      <el-table-column label=""
                       prop="content">
      </el-table-column>
<!--      <el-table-column label="html"-->
<!--                       prop="htmlContent" width="100px">-->
<!--      </el-table-column>-->
      <el-table-column label="ID"
                       prop="userId" width="80px">
      </el-table-column>
      <el-table-column label=""
                       prop="userName" width="95px">
      </el-table-column>
      <el-table-column label=""
                       prop="createDate" width="160px">
      </el-table-column>
      <el-table-column label="" prop="goods_state" width="95px">
        <template slot-scope="scope">
          <el-dropdown placement="top-end">
            <el-link :underline="false">
              <el-tag v-if="scope.row.status === -4"></el-tag>
              <el-tag v-else-if="scope.row.status === -3" type="danger"></el-tag>
              <el-tag v-else-if="scope.row.status === -2" type="info"></el-tag>
              <el-tag v-else-if="scope.row.status === -1" type="warning"></el-tag>
              <el-tag v-else type="success">:{{scope.row.status}}</el-tag>
            </el-link>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-if="scope.row.status!==-4" @click.native="updateStatus('',-4,scope.row.id,scope.row.title)"><el-tag></el-tag></el-dropdown-item>
              <el-dropdown-item v-if="scope.row.status!==-3" @click.native="updateStatus('',-3,scope.row.id,scope.row.title)"><el-tag type="danger"></el-tag></el-dropdown-item>
              <el-dropdown-item v-if="scope.row.status!==-2" @click.native="updateStatus('',-2,scope.row.id,scope.row.title)"><el-tag type="info"></el-tag></el-dropdown-item>
              <el-dropdown-item v-if="scope.row.status!==-1" @click.native="updateStatus('',-1,scope.row.id,scope.row.title)"><el-tag type="warning"></el-tag></el-dropdown-item>
              <el-dropdown-item v-if="scope.row.status<0" @click.native="updateStatus('',0,scope.row.id,scope.row.title)"><el-tag type="success"></el-tag></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
      <el-table-column label="" width="130px">
        <template slot-scope="scope">
          <!--  -->
          <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
            <el-button icon="el-icon-search" size="mini" type="primary"
                       @click="purchaseInfoDetail(scope.row.title,scope.row.content)"></el-button>
          </el-tooltip>

          <!--  -->
          <el-tooltip :enterable="false" class="item" content="" effect="dark" placement="top">
            <el-button icon="el-icon-delete" size="mini" type="danger"
                       @click="removeGoodsById(scope.row.id,scope.row.title)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-drawer
        :visible.sync="drawer"
        :with-header="false">
      <h1 style="text-align: center">{{drawerTitle}}</h1>
      <mavon-editor defaultOpen="preview" :ishljs="true" :toolbarsFlag='false' :zIndex="0" :subfield="false" ref=md :value="drawerContent"></mavon-editor>
    </el-drawer>

    <!---->
    <el-pagination
        :current-page="queryInfo.currentPage"
        :page-size="queryInfo.size"
        :page-sizes="[5,10,15,20]"
        :total="total"
        background
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
    </el-pagination>
  </div>
</template>

<script>
import {reqGetPurchaseInfoList, reqDeletePurchaseInfoById, reqUpdatePurchaseInfoStatusById} from '@/api'
export default {
  name: "PurchaseInfoList",
  data(){
    return{
      // 
      queryInfo: {
        keyword: '',
        currentPage: 1,
        size: 5,
        userId:'',
        status:'',
      },
      purchaseInfoList:[],
      loading:false,
      total:0,
      drawer:false,
      drawerTitle:'',
      drawerContent:'',
    }
  },
  created() {
    this.getPurchaseInfoList()
  },
  methods:{
    async getPurchaseInfoList(){
      try {
        let result = await reqGetPurchaseInfoList(this.queryInfo)
        this.purchaseInfoList = result.data.records
        this.total = result.data.total
      }catch (e) {
        console.log(e.message)
      }
    },
    /**
     * 
     * @param purchaseInfoId id
     * @param title 
     */
    async removeGoodsById (purchaseInfoId,title) {
      await this.$confirm('<'+purchaseInfoId+'>'+title+', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await reqDeletePurchaseInfoById({purchaseInfoId})
          if (this.total % this.queryInfo.size === 1){
            this.queryInfo.currentPage--
            await this.getPurchaseInfoList()
          }else {
            await this.getPurchaseInfoList()
          }
        }catch (e) {
          console.log(e.message)
        }
      }).catch(err => {
        return err
      })
    },
    //
    updateStatus(statusName,status,purchaseInfoId,title){
      this.$confirm('<'+purchaseInfoId+'>' + title + '<' + statusName + '>, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await reqUpdatePurchaseInfoStatusById({purchaseInfoId,status})
        await this.getPurchaseInfoList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    purchaseInfoDetail(title,content){
      this.drawer = true
      this.drawerTitle = title
      this.drawerContent = content
    },
    //
    handleSizeChange (newSize) {
      this.queryInfo.size = newSize
      this.getPurchaseInfoList()
    },
    //
    handleCurrentChange (newPage) {
      this.queryInfo.currentPage = newPage
      this.getPurchaseInfoList()
    },
  },
}
</script>

<style scoped lang="less">

</style>
