<template>
  <div class="editor">
    <el-form label-width="80px" :model="commonMessage" :rules="commonMessageRules" ref="commonMessage">
      <el-form-item label="" prop="title">
        <el-input v-model="commonMessage.title" size="large" placeholder="" maxlength="20" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <el-input v-model="commonMessage.content" size="large" placeholder="" maxlength="200" show-word-limit></el-input>
      </el-form-item>
      <el-button @click="releaseCommonMessage"></el-button>
    </el-form>

    <el-form label-width="80px" :model="bulletinBoardMessage" :rules="bulletinBoardMessageRules" ref="bulletinBoardMessage">
      <el-form-item label="" prop="title">
        <el-input v-model="bulletinBoardMessage.title" size="large" placeholder="" maxlength="20" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <mavon-editor v-model="bulletinBoardMessage.content" ref="md" @change="change"/>
      </el-form-item>
      <el-button @click="releaseBulletinBoardMessage"></el-button>
    </el-form>
  </div>
</template>

<script>
import {reqReleaseCommonMessage,reqReleaseBulletinBoardMessage} from '@/api'
export default {
  name: "ReleaseMessage",
  data(){
    return{
      commonMessage:{
        title:'',
        content:'',
      },
      bulletinBoardMessage:{
        title:'',
        content:'',
        htmlContent:'',
      },
      commonMessageRules:{
        title: [
          {required: true, message: '', trigger: 'blur'},
        ],
        content: [
          {required: true, message: '', trigger: 'blur'},
        ],
      },
      bulletinBoardMessageRules:{
        title: [
          {required: true, message: '', trigger: 'blur'},
        ],
        content: [
          {required: true, message: '', trigger: 'blur'},
        ],
      },
    }
  },
  methods:{
    async releaseCommonMessage () {
      this.$refs['commonMessage'].validate(async (valid) => {
        if (valid) {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
            try {
              let {title,content} = this.commonMessage
              await reqReleaseCommonMessage({title,content,'htmlContent':content})
              this.commonMessage = {
                title:'',
                content:'',
              }
            } catch (e) {
              console.log(e.message)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        } else {
          console.log('')
        }
      })
    },
    async releaseBulletinBoardMessage () {
      this.$refs['bulletinBoardMessage'].validate(async (valid) => {
        if (valid) {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
            try {
              let {title,content,htmlContent} = this.bulletinBoardMessage
              await reqReleaseBulletinBoardMessage({title,content,htmlContent})
              this.bulletinBoardMessage = {
                title:'',
                content:'',
                htmlContent:'',
              }
            } catch (e) {
              console.log(e.message)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        } else {
          console.log('')
        }
      })
    },
    change(value, render){
      // render  markdown 
      this.bulletinBoardMessage.htmlContent = render;
    },
  },
}
</script>

<style scoped>
.editor{
  width: 1000px;
  margin: auto;
}
</style>
