<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
<!--      v-loading="!cateList.length"-->
<!--      element-loading-text=""-->
      <tree-table
                  :columns="cateColumns"
                  :data="cateList"
                  :expand-type="false"
                  :selection-type="false"
                  :show-index="true"
                  :show-row-hover="true"
                  border
                  class="tree-table"
                  index-text="#">
        <!---->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="!scope.row.fatherId" size="mini"></el-tag>
          <el-tag v-else size="mini" type="success"></el-tag>
        </template>
        <!---->
        <template slot="opt" slot-scope="scope">
          <el-button icon="el-icon-edit" size="mini" type="primary" @click="showEditDialog(scope.row.fatherId,scope.row.id)">
          </el-button>
          <el-button icon="el-icon-delete" size="mini" type="danger" @click="removeCateById(scope.row.fatherId,scope.row.id)">
          </el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-dialog :visible.sync="addCateDialogVisible" title="" @close="addCateDialogClosed">
        <el-form ref="addCateFormRef" :model="addCateForm" :rules="addCateFormRules" label-width="100px">
          <el-form-item label=": " prop="name">
            <el-input v-model="addCateForm.name"></el-input>
          </el-form-item>
          <el-form-item label=": ">
            <el-select v-model="addCateForm.fatherId" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option
                  v-for="item in parentCateList"
                  :key="item.categoryId"
                  :label="item.categoryName"
                  :value="item.categoryId">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog :visible.sync="editCateDialogVisible" title="" @close="editCateDialogClosed">
        <el-form ref="editCateFormRef" :model="editCateForm" label-width="100px">
          <el-form-item label=": " prop="name">
            <el-input v-if="editCateForm.categoryName" v-model="editCateForm.categoryName"></el-input>
            <el-input v-else v-model="editCateForm.typeName"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="editCateDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editCate"> </el-button>
        </div>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
import {
  reqGetGoodsCategoryTree, reqGetGoodsCategoryInfo, reqGetGoodsTypeInfo, reqUpdateGoodsCategoryName,
  reqUpdateGoodsTypeName, reqAddGoodsCategory, reqAddGoodsType, reqGetCategoryListInGoodsCategory,
  reqDeleteGoodsCategory, reqDeleteGoodsType
} from '@/api'
export default {
  name: "Category",
  data(){
    return{
      // 
      addCateDialogVisible:false,
      // Id
      selectedKeys: '',
      //
      parentCateList:[],
      // 
      addCateForm: {
        // 
        name: '',
        // Id
        fatherId: '',
      },
      // 
      addCateFormRules: {
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      // 
      editCateDialogVisible:false,
      // 
      editCateForm: {},
      // , 
      cateList: [],
      // table
      cateColumns: [
        {
          label: '',
          prop: 'name'
        },
        {
          label: '',
          prop: 'createTime'
        },
        {
          label: '',
          prop: 'updateTime'
        },
        {
          label: '',
          // , 
          type: 'template',
          // 
          template: 'order'
        },
        {
          label: '',
          // , 
          type: 'template',
          // 
          template: 'opt'
        },
      ],
    }
  },
  created () {
    this.getCateList()
  },
  methods:{
    /**
     * 
     */
    async getCateList () {
      let result = await reqGetGoodsCategoryTree()
      this.cateList = result.data
    },
    /**
     * 
     */
    async getParentCateList () {
      let result = await reqGetCategoryListInGoodsCategory()
      this.parentCateList = result.data
    },
    /**
     * , 
     */
    showAddCateDialog () {
      // 
      this.getParentCateList()
      // 
      this.addCateDialogVisible = true
    },
    /**
     * , 
     */
    async addCate () {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const {fatherId, name} = this.addCateForm
        try {
          if (fatherId === ''){
            await reqAddGoodsCategory({'categoryName':name})
          }else {
            await reqAddGoodsType({'categoryId':fatherId,'typeName':name})
          }
          await this.getCateList()
          this.addCateDialogVisible = false
        }catch (e) {
          console.log(e.message)
        }
      })
    },
    /**
     * , 
     */
    addCateDialogClosed () {
      this.$refs.addCateFormRef.resetFields()
      this.addCateForm.name = ''
      this.addCateForm.fatherId = ''
    },
    /**
     * 
     * @param fatherId
     * @param id
     */
    async showEditDialog (fatherId, id) {
      let result
      try {
        if (fatherId){
          result = await reqGetGoodsTypeInfo({id})
        }else {
          result = await reqGetGoodsCategoryInfo({id})
        }
      }catch (e) {
        console.log(e.message)
      }
      this.editCateForm = result.data
      this.editCateDialogVisible = true
    },
    /**
     * id
     * @param fatherId id
     * @param id id
     */
    async removeCateById (fatherId, id) {
      // 
      await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          if (fatherId){
            await reqDeleteGoodsType({'typeId':id})
          }else {
            await reqDeleteGoodsCategory({'categoryId':id})
          }
          await this.getCateList()
        }catch (e) {
          console.log(e.message)
        }
      }).catch(async () => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    /**
     * 
     */
    async editCate () {
      this.$refs.editCateFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        if (this.editCateForm.typeId){
          await reqUpdateGoodsTypeName({
            'typeName':this.editCateForm.typeName,'typeId':this.editCateForm.typeId
          })
        }else {
          await reqUpdateGoodsCategoryName({
            'categoryName':this.editCateForm.categoryName,'categoryId':this.editCateForm.categoryId})
        }
        await this.getCateList()
        this.editCateDialogVisible = false
      })
    },
    /**
     *  
     */
    editCateDialogClosed () {
      this.$refs.editCateFormRef.resetFields()
    },
  },
}
</script>

<style scoped>

</style>
