<template>

  <!---->
  <el-container class="home-container">
    <!---->
    <el-header>
      <div>
        <img width="20px" alt="" src="@/assets/logo.png">
        <span></span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!---->
    <el-container>
      <!---->
      <el-aside v-bind:width="isCollapsed ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapsed">
          |||
        </div>
        <!---->
        <el-menu
            :collapse="isCollapsed"
            :collapse-transition="false"
            :default-active="activePath"
            :router="true"
            :unique-opened="true"
            active-text-color="#409EFF"
            background-color="#333744"
            text-color="#fff">
          <!-- -->
          <el-submenu v-for="(item) in menuList" :key="item.id" v-bind:index="item.id + ''">
            <!-- -->
            <template slot="title">
              <!--  -->
              <i v-bind:class="iconObj[item.id]"></i>
              <!--  -->
              <span>{{ item.authName }}</span>
            </template>

            <!--  -->
            <el-menu-item v-for="(subItem) in item.children" :key="subItem.id" v-bind:index="'/' + subItem.path"
                          @click="saveNavState('/' + subItem.path)">
              <!-- -->
              <template slot="title">
                <!--  -->
                <i v-bind:class="iconObj[subItem.id]"></i>
                <!--  -->
                <span>{{ subItem.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!---->
      <el-main ref="main">
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'MyIndex',
  data () {
    return {
      // 
      menuList: [],
      iconObj: {
        100: 'el-icon-s-data',
        101: 'el-icon-s-marketing',
        102: 'el-icon-picture-outline-round',
        200: 'el-icon-s-goods',
        201: 'el-icon-menu',
        202: 'el-icon-document',
        203: 'el-icon-search',
        204: 'el-icon-warning',
        300: 'el-icon-user-solid',
        301: 'el-icon-document',
        400: 'el-icon-message-solid',
        401: 'el-icon-edit',
        500: 'el-icon-shopping-cart-full',
        501: 'el-icon-document',
      },
      // 
      isCollapsed: false,
      // 
      activePath: '',
    }
  },
  created () {
    // this.getMenuList()
    this.menuList = [
        {
          id:100,
          authName:'',
          path:'',
          children:[
            {
              id:101,
              authName:'',
              path:'home',
            },
            {
              id:102,
              authName:'',
              path:'about',
            },
          ]
        },
        {
          id:200,
          authName:'',
          path:'',
          children:[
            {
              id:201,
              authName:'',
              path:'category',
            },
            {
              id:202,
              authName:'',
              path:'goodsList',
            },
            {
              id:203,
              authName:'',
              path:'goodsVerify',
            },
            {
              id:204,
              authName:'',
              path:'goodsWarn',
            },
          ]
        },
      {
        id:500,
        authName:'',
        path:'',
        children:[
          {
            id:501,
            authName:'',
            path:'purchaseInfoList',
          },
        ]
      },
      {
        id:300,
        authName:'',
        path:'',
        children:[
          {
            id:301,
            authName:'',
            path:'userList',
          },
        ]
      },
      {
        id:400,
        authName:'',
        path:'',
        children:[
          {
            id:401,
            authName:'',
            path:'releaseMessage',
          },
        ]
      },
    ]
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    async logout () {
      //
      await this.$store.dispatch('adminLogout')
      //cookieaccess_token_admin
      await this.$cookies.remove('access_token_admin')
      await this.$router.push("/login");
    },
    /**
     * 
     */
    // async getMenuList () {
    //   const { data: res } = await this.$http('menus')
    //   console.log(res)
    //   if (res.meta.status !== 200) {
    //     return this.$message.error(res.meta.msg)
    //   } else {
    //     this.menuList = res.data
    //   }
    // },
    /**
     * 
     */
    toggleCollapsed () {
      this.isCollapsed = !this.isCollapsed
    },
    /**
     * 
     */
    saveNavState (activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      //
      this.activePath = activePath
    }

  }

}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}

.el-header {
  background-color: #373D41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;

  > div {
    display: flex;
    align-items: center;

    span {
      margin-left: 15px;
    }
  }
}

.el-aside {
  background-color: #333744;
  display: block;
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #EAEDF1;
  position: absolute;
  left: 200px;
  right: 0;
  top: 60px;
  bottom: 0;
  overflow-y: scroll;
}

.iconfont {
  margin-right: 10px;
}

.toggle-button {
  background-color: #4A5064;
  font-size: 10px;
  line-height: 24px;
  color: #ffffff;
  text-align: center;
  letter-spacing: 0.2em;
  // cursor CSS 
  cursor: pointer;
}
</style>
